* {
  /* @apply text-prime-white-2 fill-prime-white-2; */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


@tailwind base;
@tailwind components;
@tailwind utilities;

.non-touchable {
  pointer-events: none;
}

.letterAnimation {
  will-change: transform;
}

.backdrop-blur-new {
  background: rgba(0, 0, 0, 0.808);
  -webkit-backdrop-filter: blur(60px);
  backdrop-filter: blur(16px);
}
.hero-title {
  font-size: clamp(30px, 5vw, 50px);
  font-family: 'Montserrat';
}

@media (min-width: 640px) {
  .hero-title {
    font-size: clamp(50px, 5vw, 10vw);
    font-weight:200;
  font-family: 'Genos';
  }
}
.exo-title {
  font-size: clamp(80px, 130px, 8vw);
  line-height: 1.1em;
  /* font-size: clamp(3.4vw, 6vw, 7vw); */
}

.banner-title {
  font-size: clamp(12px, 18px, 2vw);
}

.mid-title {
  font-size: clamp(40px, 4vw, 6vw);
}

.sub-title {
  /* font-size: clamp(0.8vw, 1.4vw, 2vw); */
  font-size: clamp(12px, 18px, 2vw);
  /* font-size: clamp(8px, 1.5vw, 1.8vw); */
}



.font-mont {
  font-family: "Montserrat", serif;
}

.font-dm {
  font-family: "Genos", "Montserrat", "DM Serif Display";
  text-transform: uppercase;
}


body {
  position: relative;
  font-family: "Montserrat";
  overflow-x: hidden;
  @apply bg-prime-white-3 text-prime-dark-2;

  /* @apply bg-prime-dark-2 */
}

/* .dark body {
  @apply bg-prime-dark-2 text-white;
} */

canvas {
  display: block;
}

nav {
  @apply flex justify-between h-[10vh] items-center p-5 z-50;
}

nav img {
  @apply h-full object-cover;
}

.profile {
  @apply h-full rounded-2xl p-3 bg-slate-100 aspect-square flex justify-center items-center;
}

.profile svg {
  @apply h-2/3 w-2/3;
}

.sidenav {
  @apply fixed top-0 right-0 w-full -z-30 h-screen transition-all duration-700 flex p-4;
}

.sidenav .leftSideNav {
  @apply flex flex-col text-9xl p-4 w-[90%] justify-center font-black uppercase pl-20;
}

.sidenav .rightSideNav {
  @apply flex flex-col font-light items-center text-2xl p-4 w-[10%];
}

.sphere canvas {
  opacity: 0;
}